html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  /* 2 */
  cursor: pointer;
  /* 3 */
  *overflow: visible;
  /* 4 */ }

/**
 * Re-set default cursor for disabled elements.
 */
button[disabled],
html input[disabled] {
  cursor: default; }

/**
 * 1. Address box sizing set to content-box in IE 8/9.
 * 2. Remove excess padding in IE 8/9.
 * 3. Remove excess padding in IE 7.
 *    Known issue: excess padding remains in IE 6.
 */
input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  /* 1 */
  padding: 0;
  /* 2 */
  *height: 13px;
  /* 3 */
  *width: 13px;
  /* 3 */ }

/**
 * 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.
 * 2. Address `box-sizing` set to `border-box` in Safari 5 and Chrome
 *    (include `-moz` to future-proof).
 */
input[type="search"] {
  -webkit-appearance: textfield;
  /* 1 */
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  /* 2 */
  box-sizing: content-box; }

/**
 * Remove inner padding and search cancel button in Safari 5 and Chrome
 * on OS X.
 */
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

/**
 * Remove inner padding and border in Firefox 3+.
 */
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0; }

/**
 * 1. Remove default vertical scrollbar in IE 6/7/8/9.
 * 2. Improve readability and alignment in all browsers.
 */
textarea {
  overflow: auto;
  /* 1 */
  vertical-align: top;
  /* 2 */ }

/**
 * Remove most spacing between table cells.
 */
table {
  border-collapse: collapse;
  border-spacing: 0; }

html,
button,
input,
select,
textarea {
  color: #222; }

::-moz-selection {
  background: #b3d4fc;
  text-shadow: none; }

::selection {
  background: #b3d4fc;
  text-shadow: none; }

img {
  vertical-align: middle; }

fieldset {
  border: 0;
  margin: 0;
  padding: 0; }

textarea {
  resize: vertical; }

.chromeframe {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0; }

body {
  padding-top: 64px; }

* {
  box-sizing: border-box; }

.container {
  max-width: 1370px;
  padding: 0 100px;
  margin: auto; }
  .container__title {
    font-family: 'Playfair Display', serif;
    font-style: normal;
    font-weight: bold;
    font-size: 48px;
    line-height: 64px;
    text-align: center;
    margin-bottom: 60px;
    color: #333333; }
  .container__slider {
    display: flex;
    align-items: flex-end;
    position: relative; }
  .container__article {
    position: absolute;
    width: 50%;
    top: 0;
    right: 0;
    font-family: 'Playfair Display', serif;
    font-style: normal;
    font-weight: bold;
    font-size: 30px;
    line-height: 130%;
    padding: 42px 120px 49px 120px; }
  .container__main {
    width: 50%;
    display: flex;
    height: 610px; }
  .container__mainlist {
    width: 100%;
    height: 100%;
    position: relative; }
    .container__mainlist .slick-list, .container__mainlist .slick-track, .container__mainlist .slick-slide > div {
      height: 100%; }
  .container__navigate {
    position: absolute;
    left: 77px;
    bottom: 30px;
    text-align: right; }
    .container__navigate > div {
      cursor: pointer;
      font-family: Gotham Pro;
      font-style: normal;
      font-weight: 200;
      font-size: 14px;
      line-height: 130%;
      text-align: right;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      color: white; }
      .container__navigate > div:first-child {
        margin-right: -26px;
        margin-bottom: 16px; }
        .container__navigate > div:first-child::after {
          content: "";
          width: 20px;
          height: 1px;
          background-color: white;
          right: -100%;
          margin-left: 6px; }
  .container__mainslide {
    height: 100%;
    width: 100%;
    background-position: center;
    background-size: cover;
    display: flex !important;
    justify-content: center;
    align-items: center;
    flex-direction: column; }
    .container__mainslide .inner_title {
      font-family: Gotham Pro;
      font-style: normal;
      font-weight: 400;
      font-size: 24px;
      line-height: 130%;
      text-align: center;
      color: #FFFFFF;
      display: flex;
      flex-direction: column;
      margin-bottom: 50px; }
    .container__mainslide a {
      display: inline-flex;
      font-family: Gotham Pro;
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 13px;
      align-items: center;
      text-align: center;
      text-transform: uppercase;
      padding: 14px 30px;
      border: 2px solid #D2A665;
      justify-content: center;
      color: white;
      text-decoration: none;
      transition: 0.3s; }
      .container__mainslide a:hover {
        background-color: #D2A665;
        color: white; }
  .container__items {
    height: 416px;
    background-color: #1C1C1E;
    width: 50%;
    display: inline-flex !important;
    align-items: flex-end;
    padding-bottom: 59px; }
    .container__items .slick-list {
      margin-left: -110px;
      overflow: hidden; }
    .container__items .slick-slide {
      width: 210px;
      height: auto;
      padding-right: 25px; }
      .container__items .slick-slide:last-child {
        padding-right: 0; }
      .container__items .slick-slide img {
        width: 100%;
        height: auto; }
  .container__item {
    cursor: pointer;
    transition: 0.3s; }
    .container__item:hover {
      opacity: 0.7; }

@media all and (max-width: 1310px) {
  .container__article {
    font-size: 24px;
    padding: 20px 100px; } }

@media all and (max-width: 1100px) {
  .container__article {
    padding: 20px 50px; } }

@media all and (max-width: 768px) {
  .container {
    padding: 0; }
    .container__navigate {
      left: 0;
      bottom: 0;
      display: flex;
      width: 100%;
      justify-content: space-between;
      padding: 25px;
      background-color: white; }
      .container__navigate div {
        margin: 0 !important;
        color: black;
        font-size: 18px; }
        .container__navigate div:after {
          display: none; }
    .container__slider {
      flex-wrap: wrap; }
    .container__main {
      width: 100%;
      position: relative; }
    .container__items {
      padding-top: 50px;
      width: 100%;
      height: auto; }
      .container__items .slick-list {
        margin-left: 25px; }
    .container__article {
      position: static;
      width: 100%;
      text-align: center;
      font-size: 24px;
      padding: 20px 0; } }

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  user-select: none;
  touch-action: pan-y; }

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0; }
  .slick-list:focus {
    outline: none; }
  .slick-list.dragging {
    cursor: pointer;
    cursor: hand; }

.slick-slider .slick-track,
.slick-slider .slick-list {
  transform: translate3d(0, 0, 0); }

.slick-track:before, .slick-track:after {
  content: "";
  display: table; }

.slick-track:after {
  clear: both; }

.slick-loading .slick-track {
  visibility: hidden; }

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none; }
  [dir="rtl"] .slick-slide {
    float: right; }
  .slick-slide img {
    display: block; }
  .slick-slide.slick-loading img {
    display: none; }
  .slick-slide.dragging img {
    pointer-events: none; }
  .slick-initialized .slick-slide {
    display: block; }
  .slick-loading .slick-slide {
    visibility: hidden; }
  .slick-vertical .slick-slide {
    display: block;
    height: auto;
    border: 1px solid transparent; }

.slick-arrow.slick-hidden {
  display: block; }
